@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@500&display=swap);
body{
  background-color: bisque;
}

* {
  font-family: 'Baloo Tamma 2', cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar{
  background-color: #fff;
}

.navbar-brand{
  font-size: 1.9rem;
  color: #565185;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565185;
}

.navbar a:hover{
  color: #3497da;
}

@media (max-width: 991px){
  .navbar-brand{
    font-size: 1.5rem;
  }

.menu_active{
    font-weight: bold;
    border-bottom: none;
  }
}

.tj{
  text-align: justify;
}

.pv4 {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.pt4 {
  padding-top: 4rem;
}

.ph4 {
  padding-left: 2rem;
  padding-right: 2rem;
}

.ph8 {
  padding-left: 4rem;
  padding-right: 4rem;
}

.pb2 {
  padding-bottom: 2rem;
  margin-bottom: 10px;
}

.pv2 {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.ph2 {
  padding-left: 1rem;
  padding-right: 1rem;
}

